stive', cursive;font-size:100px;margin-bottom:0}.light-and-dark-mode-container h2{font-size:32px;font-weight:normal;color:var(--on-background-alt)}.light-and-dark-mode-container .title-group{text-align:center}.light-and-dark-mode-container .about-container{display:flex}.light-and-dark-mode-container .image-container{border:1px solid var(--secondary-color);border-radius:10px;padding:10px 20px;margin-right:25px;width:auto;background:var(--background);box-shadow:var(--box-shadow)}.light-and-dark-mode-container img{height:300px;width:300px}.light-and-dark-mode-container .buttons{margin-top:15px;margin-bottom:50px}.light-and-dark-mode-container .buttons button{min-width:100px;height:40px;cursor:pointer;border-radius:10px;margin-right:10px;border:2px solid var(--primary-color);font-size:15px;outline:none}.light-and-dark-mode-container .buttons button:disabled{opacity:0.4;cursor:default}.light-and-dark-mode-container .buttons button:hover:not(.outline){-webkit-filter:brightness(110%);filter:brightness(110%)}.light-and-dark-mode-container .buttons button.primary{background:var(--primary-color);color:var(--on-primary)}.light-and-dark-mode-container .buttons button.secondary{border:2px solid var(--secondary-color)}.light-and-dark-mode-container .buttons button.secondary,.light-and-dark-mode-container .buttons button.secondary:hover,.light-and-dark-mode-container .buttons button.outline.secondary:hover{background:var(--secondary-color);color:var(--on-primary)}.light-and-dark-mode-container .buttons button.outline{background:var(--background);color:var(--on-background)}.light-and-dark-mode-container .buttons button.outline:hover{background:var(--primary-color);color:var(--on-primary)}.light-and-dark-mode-container .text-box{width:40%;text-align:justify;background:rgba(0,0,0,0.5);color:var(--on-primary);border-radius:10px;padding:30px;font-size:1.6rem}.light-and-dark-mode-container p{margin:0;line-height:25px}.light-and-dark-mode-container .fab{font-size:100px;margin-right:50px;cursor:pointer;color:var(--primary-color)}.light-and-dark-mode-container .fab:hover{color:var(--on-background)}.light-and-dark-mode-container .theme-switch-wrapper{display:flex;align-items:center;justify-content:center;z-index:100;position:fixed;right:25px;top:30px;margin-top:var(--navbar-size)}.light-and-dark-mode-container .theme-switch-wrapper .toggle-icon{display:flex;justify-content:center;align-items:center}.light-and-dark-mode-container .theme-switch-wrapper .toggle-icon .toggle-text{color:var(--on-background);font-size:1.6rem}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch{display:inline-block;height:34px;position:relative;width:60px}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch input{display:none}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch input:checked+.slider{background:var(--primary-color)}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch input:checked+.slider::before{-webkit-transform:translateX(26px);transform:translateX(26px)}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch .slider{background:#ccc;bottom:0;cursor:pointer;left:0;position:absolute;right:0;top:0;transition:0.4s}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch .slider.round{border-radius:34px}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch .slider.round::before{border-radius:50%}.light-and-dark-mode-container .theme-switch-wrapper .theme-switch .slider::before{background:#fff;bottom:4px;content:'';height:26px;left:4px;position:absolute;transition:0.4s;width:26px}.light-and-dark-mode-container .theme-switch-wrapper span{margin-right:10px;font-size:1rem}.light-and-dark-mode-container .fas{font-size:30px;margin-right:5px}

